.fast-call{margin:0 auto;display:none;position:relative;padding:56px 15px 64px;width:752px;border-radius:4px;border:1px solid rgba(169,177,197,.37);background-color:#f8f8f8}@media (max-width:1023px){.fast-call{width:650px}}@media (max-width:767px){.fast-call{width:90%;min-width:300px}}.fast-call__img-box{margin:0 auto 20px;width:96px;height:auto}@media (max-width:767px){.fast-call__img-box{width:80px}}.fast-call__img{width:100%;height:100%;-o-object-fit:contain;object-fit:contain}.fast-call__title{font-size:28px;font-weight:500;color:#2f3137;text-align:center;margin-bottom:40px}@media (max-width:1023px){.fast-call__title{font-size:25px;margin-bottom:30px}}@media (max-width:767px){.fast-call__title{font-size:23px;margin-bottom:25px}}.fast-call__input-box{width:368px;margin:0 auto}@media (max-width:767px){.fast-call__input-box{width:100%}}.fast-call__button{cursor:pointer;height:56px;font-size:16px;margin-top:48px}@media (max-width:1023px){.fast-call__button{margin-top:35px}}@media (max-width:767px){.fast-call__button{height:45px}}.fast-call__close{position:absolute;right:24px;top:24px;cursor:pointer}@media (max-width:767px){.fast-call__close{right:15px;top:15px}}