.select-country{cursor:pointer;position:relative;border:1px solid #d1d7ed;background:#f8f8f8;padding:16px 60px 16px 16px;border-radius:4px;margin-bottom:24px}.select-country:last-child{margin-bottom:0}.select-country:hover{-webkit-box-shadow:0 4px 17px 0 rgba(158,179,198,.38);box-shadow:0 4px 17px 0 rgba(158,179,198,.38);background:#415ab7}@media (max-width:767px){.select-country{padding:16px 50px 16px 16px}}.select-country__title{font-size:14px;font-weight:500;color:#2f3137;margin-bottom:10px}.select-country:hover .select-country__title{color:#f8f8f8}.select-country__icons{display:-webkit-box;display:-ms-flexbox;display:flex}.select-country__icon{margin-right:16px;width:24px;height:24px;background:#415ab7;mask-repeat:no-repeat!important;-webkit-mask-repeat:no-repeat!important;mask-position:center center!important;-webkit-mask-position:center center!important;mask-size:contain!important;-webkit-mask-size:contain!important}.select-country__icon:last-child{margin-right:0}.select-country__icon--edit{mask:url(src/blocks/select-country/assets/img/edit-2.svg);-webkit-mask:url(src/blocks/select-country/assets/img/edit-2.svg)}.select-country__icon--delete{mask:url(src/blocks/select-country/assets/img/delete-2.svg);-webkit-mask:url(src/blocks/select-country/assets/img/delete-2.svg)}.select-country:hover .select-country__icon{background:#f8f8f8}.select-country__status{width:40px;height:40px;border-radius:100%;position:absolute;top:50%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);right:16px;opacity:0;visibility:hidden}.select-country:hover .select-country__status{opacity:1;visibility:visible}@media (max-width:767px){.select-country__status{width:30px;height:30px}}.select-country__status-image{width:100%;height:100%;-o-object-fit:contain;object-fit:contain}