.modal-city{display:none;border:1px solid pink;width:752px;border-radius:4px;background:#fff;padding:24px 24px 40px;position:relative}@media (max-width:1023px){.modal-city{width:550px;padding:40px}}@media (max-width:767px){.modal-city{width:420px;padding:30px}}@media (max-width:440px){.modal-city{width:300px}}.modal-city__img{padding:24px 0 40px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center}.modal-city__title{font-weight:500;font-size:28px;color:#2f3137;line-height:42px;text-align:center;margin-bottom:40px}@media (max-width:767px){.modal-city__title{font-size:24px;margin-bottom:30px}}@media (max-width:440px){.modal-city__title{font-size:20px;margin-bottom:20px}}.modal-city__select-city{font-size:14px;color:#2f3137;line-height:30px}.modal-city__select-city:hover{color:#415ab7;font-weight:500}.modal-city__box{position:relative;width:368px;height:56px;margin:0 auto}@media (max-width:440px){.modal-city__box{width:100%}}.modal-city__input{font-family:Ubuntu,sans-serif;font-size:15px;color:#2f3137;margin-right:16px;border:1.6px solid rgba(65,90,183,.2);background:hsla(0,0%,100%,.1);padding:24px 16px 8px;border-radius:4px;width:100%;height:100%}.modal-city__input:hover{-webkit-box-shadow:0 2px 15px rgba(65,90,183,.12);box-shadow:0 2px 15px rgba(65,90,183,.12)}.modal-city__input:focus{border-color:#415ab7}.modal-city__label{position:absolute;left:16px;top:36%;font-size:15px;color:rgba(47,49,55,.4)}.modal-city__label-active,.modal-city__label-color{color:rgba(47,49,55,.4);font-size:14px;top:10px}.modal-city__list{width:336px;margin:16px auto 0;height:232px}.modal-city__close{position:absolute;right:24px;top:24px}