.breadcrumbs{padding:32px 0}@media (max-width:767px){.breadcrumbs{padding:20px 0}}.breadcrumbs__inner{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-ms-flex-wrap:wrap;flex-wrap:wrap}.breadcrumbs__separator{margin:0 15px;width:7px;height:11px;background:#2f3137;mask:url(src/images/icons/separator-breadcrumbs.svg);-webkit-mask:url(src/images/icons/separator-breadcrumbs.svg);mask-repeat:no-repeat!important;-webkit-mask-repeat:no-repeat!important;mask-position:center center!important;-webkit-mask-position:center center!important;mask-size:contain!important;-webkit-mask-size:contain!important}.breadcrumbs__separator--white{background:hsla(0,0%,100%,.5)}@media (max-width:767px){.breadcrumbs__separator{margin:0 7px}}.breadcrumbs__link{font-size:14px;color:#415ab7}.breadcrumbs__link:hover{color:#2f428c}.breadcrumbs__link--white,.breadcrumbs__link--white:hover{color:#f8f8f8}@media (max-width:767px){.breadcrumbs__link{font-size:12px;line-height:17px}}.breadcrumbs__link.active{color:rgba(47,49,55,.6);cursor:default}.breadcrumbs__link.active.active--white{color:hsla(0,0%,100%,.5)}