.modal-complete-company{display:none;margin:0 auto;position:relative;background:#fff;width:1056px;height:calc(100vh - 150px);overflow-y:auto}.modal-complete-company::-webkit-scrollbar{width:5px}.modal-complete-company::-webkit-scrollbar-thumb{background-color:#415ab7;border-radius:3px}.modal-complete-company::-webkit-scrollbar-track{background:rgba(0,0,0,0)}@media (max-width:1023px){.modal-complete-company{width:700px}}@media (max-width:767px){.modal-complete-company{width:95%;min-width:300px}}