.button{padding:18px 48px;background:#38c17b;border-radius:5px;font-weight:500;font-size:20px;line-height:23px;color:#f8f8f8;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center}.button__arrow{margin-left:22px}@media (max-width:767px){.button__arrow{margin-left:10px}.button__arrow img{width:19px;height:auto}}.button__dots{margin-right:12px}@media (max-width:767px){.button__dots img{width:15px;height:auto}}.button--white{background:#f8f8f8;color:#2f3137}@media (max-width:767px){.button{padding:12px;font-size:16px}}