.watch-video{padding:55px 15px 60px;background:-webkit-gradient(linear,left top,left bottom,from(#506dd8),to(#415ab7));background:-o-linear-gradient(#506dd8,#415ab7);background:linear-gradient(#506dd8,#415ab7);position:relative;z-index:0}@media (max-width:767px){.watch-video{padding:30px 15px}}.watch-video:after{content:"";background:url(src/blocks/watch-video/assets/img/decor-1.svg) no-repeat bottom;width:100%;height:433px;bottom:0;left:19%;position:absolute;-webkit-transform:translateX(-30%);-ms-transform:translateX(-30%);transform:translateX(-30%);z-index:-1}@media (max-width:767px){.watch-video:after{height:100%;background-size:70%}}.watch-video:before{content:"";width:100%;z-index:-1;height:100%;bottom:0;left:40%;position:absolute;-webkit-transform:translateX(-40%);-ms-transform:translateX(-40%);transform:translateX(-40%);background:url(src/blocks/watch-video/assets/img/rectangle.svg) no-repeat 50%}.watch-video__video-box{width:672px;height:auto;margin:0 auto}@media (max-width:1023px){.watch-video__video-box{width:550px}}@media (max-width:767px){.watch-video__video-box{width:290px}}.watch-video__video{width:100%;height:100%;-o-object-fit:contain;object-fit:contain}