.thanks{display:none;position:relative;padding:72px 15px 64px;width:752px;border-radius:4px;border:1px solid rgba(169,177,197,.37);background-color:#f8f8f8;text-align:center;margin:0 auto}@media (max-width:1023px){.thanks{width:650px}}@media (max-width:767px){.thanks{width:90%;min-width:300px}}.thanks__title{font-size:28px;font-weight:500;color:#2f3137;margin-bottom:32px}@media (max-width:1023px){.thanks__title{font-size:25px}}@media (max-width:767px){.thanks__title{font-size:23px;margin-bottom:25px}}.thanks__text{font-size:14px;color:#2f3137;line-height:1.71}.thanks__close{position:absolute;right:24px;top:24px;cursor:pointer}@media (max-width:767px){.thanks__close{right:15px;top:15px}}.thanks__image-box{width:64px;height:auto;-o-object-fit:contain;object-fit:contain;margin:0 auto 40px}@media (max-width:767px){.thanks__image-box{width:55px;margin-bottom:30px}}