.benefit{overflow:hidden;background:rgba(65,90,183,.05);padding:64px 0}@media (max-width:1023px){.benefit{padding:30px 0}}@media (max-width:767px){.benefit{padding:25px 0}}.benefit__carousel{padding:0 30px}@media (max-width:1439px){.benefit__carousel{padding:0 40px}}@media (max-width:767px){.benefit__carousel{padding:0 10px}}.benefit__carousel .owl-stage-outer{overflow:visible}.benefit__carousel .owl-item{opacity:0;-webkit-transition:opacity .5s;-o-transition:opacity .5s;transition:opacity .5s}.benefit__carousel .owl-item.active{opacity:1}.benefit__carousel button:hover{background:rgba(0,0,0,0)!important}.benefit__carousel .owl-nav{margin:0}.benefit__carousel .disabled{opacity:1!important}.benefit__carousel .owl-prev{width:40px;height:40px;top:45%;left:-1%;position:absolute}@media (max-width:767px){.benefit__carousel .owl-prev{left:-10px;width:22px;height:22px}}.benefit__carousel .owl-prev:after{content:"";display:block;height:100%;width:100%;background:url(src/blocks/benefit/assets/img/slide-left.svg) no-repeat;background-position:50%}@media (max-width:767px){.benefit__carousel .owl-prev:after{background-size:90%}}.benefit__carousel .owl-next{width:40px;height:40px;top:45%;right:-1%;position:absolute}@media (max-width:767px){.benefit__carousel .owl-next{right:-10px;width:22px;height:22px}}.benefit__carousel .owl-next:after{content:"";display:block;height:100%;width:100%;background:url(src/blocks/benefit/assets/img/slide-right.svg) no-repeat;background-position:50%}@media (max-width:767px){.benefit__carousel .owl-next:after{background-size:90%}}