.header{z-index:300;background:#f5f7fb;height:64px;position:fixed;left:0;top:0;width:100%;-webkit-transition:all .1s linear;-o-transition:all .1s linear;transition:all .1s linear}@media (max-width:767px){.header{padding:5px 0}}.header__inner{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;padding:16px 0}.header__logo-hidden{display:none;margin-right:20px}@media (max-width:1279px){.header__logo-hidden{display:block;margin-right:auto}}.header__logo-img-hidden{width:100%;height:100%}.header__burger-menu{display:none}@media (max-width:767px){.header__burger-menu{display:block}}.header__icon{display:none;margin-right:15px;width:20px;height:20px;background:#2f3137;mask-repeat:no-repeat!important;-webkit-mask-repeat:no-repeat!important;mask-position:center center!important;-webkit-mask-position:center center!important;mask-size:contain!important;-webkit-mask-size:contain!important}.header__icon:last-child{margin-right:0}.header__icon--user{mask:url(src/blocks/header/assets/img/user.svg);-webkit-mask:url(src/blocks/header/assets/img/user.svg)}.header__icon--basket{mask:url(src/blocks/header/assets/img/basket.svg);-webkit-mask:url(src/blocks/header/assets/img/basket.svg)}@media (max-width:767px){.header__icon{display:block}}