.vacancies{background:rgba(65,90,183,.05);padding-bottom:64px}@media (max-width:767px){.vacancies{padding-bottom:30px}}.vacancies__subtitle{font-size:18px;color:rgba(47,49,55,.7);text-align:center;margin-bottom:64px}@media (max-width:1023px){.vacancies__subtitle{margin-bottom:40px}}@media (max-width:767px){.vacancies__subtitle{margin-bottom:30px}}.vacancies__title{margin-bottom:16px}.vacancies__grid{display:grid;grid-template-columns:repeat(3,1fr);grid-gap:16px}@media (max-width:1279px){.vacancies__grid{grid-template-columns:repeat(auto-fill,minmax(300px,1fr))}}.vacancies__item{padding:24px 16px 16px;border-radius:4px;-webkit-box-shadow:0 0 3px 0 rgba(169,177,197,.37);box-shadow:0 0 3px 0 rgba(169,177,197,.37);background-color:#f8f8f8}.vacancies__item:hover{-webkit-box-shadow:0 6px 40px 0 rgba(158,179,198,.6);box-shadow:0 6px 40px 0 rgba(158,179,198,.6)}.vacancies__item-title{font-size:21px;font-weight:500;color:#2f3137;line-height:1.52;margin-bottom:16px}@media (max-width:767px){.vacancies__item-title{font-size:18px}}.vacancies__item-amount{font-size:21px;line-height:1.52;margin-bottom:16px}@media (max-width:767px){.vacancies__item-amount{font-size:18px}}.vacancies__item-description{line-height:1.71;font-size:14px;color:#2f3137;margin-bottom:32px}.vacancies__item-button{width:136px;height:40px}@media (max-width:767px){.vacancies__item-button{width:100%}}.vacancies__button-all{margin:32px auto}.vacancies__pagination{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center}