.modal-position{position:fixed;left:50%;top:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%);z-index:500}@media (max-width:1279px){.modal-position{width:100%}}.modal-overlay{display:none;position:fixed;top:0;left:0;width:100%;height:100%;background:#373a46;opacity:.65;z-index:400}.modal-title{text-align:center;font-weight:500;font-size:28px;color:#2f3137;line-height:42px}.modal-title__small{font-size:21px}@media (max-width:767px){.modal-title{font-size:20px;line-height:25px}}.modal-close{right:24px;top:24px;position:absolute}