.reset-password{padding:92px 15px 64px;display:none;position:relative;width:944px;border-radius:4px;border:1px solid rgba(169,177,197,.37);background-color:#f8f8f8;margin:0 auto}@media (max-width:1023px){.reset-password{width:750px}}@media (max-width:767px){.reset-password{padding:45px 15px 40px;width:90%;min-width:300px}}.reset-password__title{text-align:center;font-size:28px;font-weight:500;color:#2f3137;margin-bottom:64px}@media (max-width:1023px){.reset-password__title{font-size:25px;margin-bottom:40px}}@media (max-width:767px){.reset-password__title{font-size:23px;margin-bottom:25px}}.reset-password__text{text-align:center;font-size:14px;line-height:1.71;color:#2f3137;width:65%;margin:0 auto 32px}@media (max-width:767px){.reset-password__text{width:90%}}.reset-password__close{position:absolute;right:24px;top:24px;cursor:pointer}@media (max-width:767px){.reset-password__close{right:15px;top:15px}}.reset-password__box{margin:0 auto;width:381px}@media (max-width:767px){.reset-password__box{width:100%}}.reset-password__button{height:56px;font-size:16px;margin-top:32px}@media (max-width:767px){.reset-password__button{margin-top:25px;height:45px}}.reset-password__checkbox{margin-top:32px}@media (max-width:767px){.reset-password__checkbox{margin-top:25px}}