.search-panel{position:relative;height:48px;border-radius:4px 0 0 4px;display:grid;grid-template-columns:1fr 128px;width:100%;-webkit-transition:all .1s linear;-o-transition:all .1s linear;transition:all .1s linear}.scroll-panel--scroll .search-panel{height:38px}@media (max-width:767px){.search-panel{grid-template-columns:1fr 50px;height:30px}}.search-panel__icon{position:absolute;top:50%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);left:16px;width:20px;height:20px;background-color:#d6dae1;mask:url(src/blocks/search-panel/assets/img/search.svg);-webkit-mask:url(src/blocks/search-panel/assets/img/search.svg);mask-repeat:no-repeat!important;-webkit-mask-repeat:no-repeat!important;mask-position:center center!important;-webkit-mask-position:center center!important;mask-size:contain!important;-webkit-mask-size:contain!important}@media (max-width:767px){.search-panel__icon--mobile-hidden{display:none}}.search-panel__icon--mobile-show{display:none;position:static;margin:0 auto;width:15px;height:18px;background-color:#afd6f6}@media (max-width:767px){.search-panel__icon--mobile-show{display:block;-webkit-transform:translateY(-5%);-ms-transform:translateY(-5%);transform:translateY(-5%)}}.search-panel__input{font-size:20px;padding-left:50px;height:100%;border:1px solid #dce0e5;border-right:none;border-radius:4px 0 0 4px}@media (max-width:767px){.search-panel__input{padding-left:15px;font-size:14px}}.search-panel__button{background:#415ab7;border-radius:0 4px 4px 0;color:#fff;font-size:15px;font-weight:500;height:100%}.search-panel__button:hover{background:#3a52ab}.search-panel__button:active{background:#354b9b}@media (max-width:767px){.search-panel__text{display:none}}