.information-panel{background:#fff;position:relative;z-index:300;padding:13px 0;border-bottom:1px solid #eceef1;-webkit-transition:all .1s linear;-o-transition:all .1s linear;transition:all .1s linear}.scroll-panel--scroll .information-panel{padding:5px 0}@media (max-width:1023px){.information-panel{padding-bottom:0}}@media (max-width:767px){.information-panel{padding:5px 0}}.information-panel__inner{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;align-items:center}@media (max-width:1279px){.information-panel__inner{-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column}}@media (max-width:1279px){.information-panel__logo-link{display:none}}.information-panel__search{width:60%}@media (max-width:1279px){.information-panel__search{width:100%}}@media (max-width:1023px){.information-panel__search{width:100%!important}}.information-panel__user-panel{width:30%}@media (max-width:1279px){.information-panel__user-panel{width:100%;padding-top:10px}}@media (max-width:1023px){.information-panel__user-panel{padding:15px 0!important}}@media (max-width:767px){.information-panel__user-panel{padding:0!important}}.information-panel__box{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;width:60%;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between}@media (max-width:1279px){.information-panel__box{width:100%}}.information-panel__burger-menu{display:none}@media (max-width:1279px){.information-panel__burger-menu{display:block;margin-right:30px}}@media (max-width:767px){.information-panel__burger-menu--mobile-hidden{display:none!important}}