.modal{display:none}.modal__block{width:752px;margin:0 auto;padding:72px 24px 64px;background:#fff;border-radius:4px;-webkit-box-shadow:0 0 3px 0 rgba(169,177,197,.37);box-shadow:0 0 3px 0 rgba(169,177,197,.37);position:relative}@media (max-width:767px){.modal__block{width:95%}}.modal__inner{width:385px;margin:0 auto}@media (max-width:767px){.modal__inner{width:100%}}.modal__title{font-size:28px;font-weight:500;color:#2f3137;margin-bottom:48px;text-align:center}@media (max-width:1023px){.modal__title{font-size:25px;margin-bottom:30px}}@media (max-width:767px){.modal__title{font-size:23px;margin-bottom:25px}}.modal__box{margin-bottom:24px}.modal__checkbox{margin-bottom:32px}.modal__button{height:56px;font-size:16px}.modal__close{cursor:pointer;position:absolute;right:24px;top:24px}.modal__overlay{position:fixed;top:0;left:0;width:100%;height:100%;background:#373a46;opacity:.65;z-index:400}.modal__position{position:fixed;left:50%;top:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%);z-index:500}@media (max-width:1279px){.modal__position{width:100%}}